import React, { Component } from "react";
import uuid from "uuid";
import { experienceValidator } from "../../../../utils/validation/validateProfile";
import DatePicker from "../../../reusable/DatePicker";
import $ from "jquery";
import BarLoader from "../../../reusable/loaders/BarLoader";
import moment from "moment";
import { profile } from "../../../../utils/fixtures";

const ExpModal = ({
  title,
  at,
  companyCountry,
  companyState,
  onChangeHandler,
  getDateStateFromDatePicker,
  onExperienceSave,
  errors
}) => (
  <div
    className="modal fade avoid-navbar exp-modal"
    id="ExpModal"
    tabIndex="-1"
    role="dialog"
    aria-labelledby="experienceModalTitle"
    aria-hidden="true"
  >
    <div className="modal-dialog modal-dialog-scrollable" role="document">
      <div className="modal-content">
        <div className="modal-header">
          <h2 className="modal-title" id="experienceModalTitle">
            Add Experience
          </h2>
        </div>
        <div className="modal-body">
          {errors && (
            <p className="text-danger mb-2">missing required fields!</p>
          )}
          <input
            className="exp-input"
            name="title"
            placeholder="job title.."
            value={title}
            onChange={onChangeHandler}
          />
          <input
            className="exp-input"
            name="at"
            placeholder="company name.."
            value={at}
            onChange={onChangeHandler}
          />

          <div className="d-flex select-container">
            <input
              className="w-50 mr-1 exp-input"
              name="companyCountry"
              placeholder="country.."
              value={companyCountry}
              onChange={onChangeHandler}
              list="countries"
            />
            <datalist id="countries">
              <option value="Egypt" />
            </datalist>

            <input
              className="w-50 ml-1 exp-input"
              name="companyState"
              placeholder="state.."
              value={companyState}
              onChange={onChangeHandler}
              list="governs"
            />
            <datalist id="governs">
              {profile.states.map(st => (
                <option key={st} value={st}>
                  {st}
                </option>
              ))}
            </datalist>
          </div>

          <DatePicker updateCallerState={getDateStateFromDatePicker} />
        </div>
        <div className="modal-footer">
          <button
            type="button"
            className="btn btn-secondary"
            data-dismiss="modal"
          >
            Close
          </button>
          <button
            onClick={onExperienceSave}
            type="button"
            className="btn btn-primary"
          >
            Save changes
          </button>
        </div>
      </div>
    </div>
  </div>
);
const Exp = ({
  title,
  at,
  location,
  from,
  to,
  _deleteProfileField,
  index,
  isPreviewing
}) => (
  <div className="experience ">
    <div className="d-flex w-100 justify-content-between deletion-container">
      <h6 className="experience__title">{title}</h6>
      {!isPreviewing && (
        <i
          className="fas fa-times btn--danger-delete mr-5"
          onClick={() => {
            _deleteProfileField("experience", index);
          }}
        />
      )}
    </div>
    <h6 className="experience__subtitle">{at}</h6>
    <span className="experience__place text-muted">{location}</span>
    <span className="experience__date text-muted">
      {moment(from).format("MMM YYYY")} - {moment(to).format("MMM YYYY")}
    </span>
  </div>
);

class Experience extends Component {
  /* title:String,
        at:String,
        location:String,
        from:Date,
        to:Date 
  */
  constructor(props) {
    super(props);
    this.state = {
      title: "",
      at: "",
      companyCountry: "",
      companyState: "",
      startDate: null,
      endDate: null,
      errors: false
    };
  }

  //lifecycle

  componentDidMount() {
    const that = this;
    $("#ExpModal").on("hidden.bs.modal", () => {
      that.clearState();
    });
  }

  clearState = () =>
    this.setState({
      title: "",
      companyCountry: "",
      at: "",
      companyState: "",
      startDate: null,
      endDate: null,
      errors: false
    });

  onChangeHandler = ({ target }) => {
    this.setState({ ...this.state, [target.name]: target.value });
  };

  getDateStateFromDatePicker = (startDate = undefined, endDate = undefined) =>
    this.setState({ ...this.state, startDate, endDate });

  onExperienceSave = () => {
    const { isValid } = experienceValidator(this.state);
    if (!isValid) {
      this.setState({ ...this.state, errors: true });
      return;
    }

    const data = {
      title: this.state.title,
      at: this.state.at,
      location: `${this.state.companyCountry} , ${this.state.companyState}`,
      from: this.state.startDate,
      to: this.state.endDate
    };
    //edit
    this.props._editProfile({ experience: data });
    $("#ExpModal").modal("hide");
  };
  render() {
    const { experiences, isPreviewing = false } = this.props;
    const { errors } = this.state;
    return (
      <div className="profile__experiences main-card-layout relative-container">
        <h6 className="main-card-layout__title">Experience</h6>
        {!isPreviewing && (
          <button
            className="btn--add-plus btn btn-primary rounded-circle"
            type="button"
            data-toggle="modal"
            data-target="#ExpModal"
          >
            <i className="fas fa-plus text-light" />
          </button>
        )}
        {experiences === null ? (
          <BarLoader />
        ) : experiences.length > 0 ? (
          experiences.map((exp, index) => (
            <Exp
              key={uuid()}
              {...exp}
              _deleteProfileField={this.props._deleteProfileField}
              index={index}
              isPreviewing={isPreviewing}
            />
          ))
        ) : (
          "No experiences added"
        )}
        {!isPreviewing && (
          <ExpModal
            {...this.state}
            onChangeHandler={this.onChangeHandler}
            getDateStateFromDatePicker={this.getDateStateFromDatePicker}
            onExperienceSave={this.onExperienceSave}
            errors={errors}
          />
        )}
      </div>
    );
  }
}
export default Experience;
