import React, { Component } from "react";
import { connect } from "react-redux";
import { fetchProfile } from "../../../redux/actions";
import Loader1 from "../../reusable/loaders/Loader_1";
import {
  Brief,
  Skills,
  Education,
  Experience,
  QuestionBox,
  LanguagesRating,
  Certificate,
  ExtraInfo,
  Languages
} from "./profileComponents";
import moment from "moment";
import { availableLanguages } from "../../../utils/fixtures";
import uuid from "uuid";

export class Profile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true, //should strt with true
      chosenLang: "en"
    };
  }
  componentDidMount() {
    const { applicantId } = this.props.match.params;
    if (applicantId) {
      this.props.fetchProfile(false, applicantId, true);
    }
  }

  componentDidUpdate(prevProps, prevState) {
    if (
      this.state.isLoading === true &&
      this.props.loaders.shownProfileFetching === false
    ) {
      this.setState({ ...this.state, isLoading: false });
    }
  }

  //methods
  setChosenLang = ({ currentTarget }) => {
    this.setState({ ...this.state, chosenLang: currentTarget.value });
  };
  render() {
    if (this.state.isLoading) {
      return <Loader1 />;
    } else {
      const { profileData: pd } = this.props;
      const { user: ud } = pd;
      const { chosenLang } = this.state;
      //profile data
      const image = pd && pd.image;
      const job_title = pd && pd.job_title;
      const skills = pd && pd.skills;
      const experience = pd && pd.experience;
      const education = pd && pd.education;
      const languages = pd && pd.languages;
      const certifications = pd && pd.certifications;
      const basic_info = pd && pd.basic_info;
      const questions = pd && pd.questions && pd.questions[chosenLang];
      //user data
      const name = ud && ud.name;
      const last_logout = ud && ud.last_logout;

      return (
        <div className="profile">
          <div className="container">
            <aside className="profile__left-widget">
              <div className="profile__left-widget__wrapper">
                <Brief
                  img={!image ? "http://placehold.it/100/ddd" : image}
                  profession={job_title || "Job Title"}
                  name={name || "user name"}
                  lastActiveDate={
                    last_logout
                      ? moment(last_logout).format("hh:mm a")
                      : moment().format("hh:mm a")
                  }
                  isPreviewing={true}
                />

                <Skills skills={skills || null} isPreviewing={true} />

                <ExtraInfo basic_info={basic_info} isPreviewing={true} />
              </div>
            </aside>
            <div className="non-fixed-container">
              <main className="profile__main">
                <Languages languages={languages} isPreviewing={true} />

                <Experience
                  experiences={experience || []}
                  _deleteProfileField={this._deleteProfileField}
                  isPreviewing={true}
                />

                <Education
                  educations={education || []}
                  _deleteProfileField={this._deleteProfileField}
                  isPreviewing={true}
                />

                <h6
                  style={{ borderRadius: "0" }}
                  className="main-card-layout text-secondary text-center"
                >
                  Questions Answers
                </h6>
                <select
                  value={this.state.chosenLang}
                  style={{ borderRadius: "0" }}
                  className="flex-grow-1 w-100 mb-4 mr-2 btn border select-language main-card-layout"
                  onChange={this.setChosenLang}
                >
                  {availableLanguages.map(lang => (
                    <option key={uuid()} value={lang.value}>
                      {lang.name}
                    </option>
                  ))}
                </select>
                {questions &&
                  (questions.length > 0 ? (
                    <>
                      {questions.map((q, index) => {
                        return (
                          <QuestionBox
                            key={index.toString()}
                            index={index}
                            videoLink={q.link}
                            title={q.title}
                            isPreviewing={true}
                          />
                        );
                      })}
                    </>
                  ) : (
                    <p className="text-center">no avail questions</p>
                  ))}
              </main>

              <aside className="profile__right-widget">
                <LanguagesRating languages={languages} isPreviewing={true} />
                {/* 
                <Certificate
                  certifications={certifications || []}
                  _editProfile={this._editProfile}
                  isPreviewing={true}
                /> */}
              </aside>
            </div>
          </div>
        </div>
      );
    }
  }
}

const mapStateToProps = state => {
  return {
    profileData: state.profile.currentShownProfile,
    userData: state.user.userData,
    loaders: { ...state.profile.loaders }
  };
};

export default connect(mapStateToProps, {
  fetchProfile
})(Profile);
