import React, { Component } from "react";
import $ from "jquery";
import { profile } from "../../../../utils/fixtures";

const ExtraInfoModal = ({
  age,
  graduated,
  live_in_country,
  live_in_state,
  onChangeHandler,
  onSave,
  errors
}) => (
  <div
    className="modal fade avoid-navbar exp-modal"
    id="ExtraInfoModal"
    tabIndex="-1"
    role="dialog"
    aria-labelledby="ExtraInfoModalTitle"
    aria-hidden="true"
  >
    <div className="modal-dialog  " role="document">
      <div className="modal-content">
        <div className="modal-header">
          <h3 className="modal-title" id="ExtraInfoModalTitle">
            Add basic info
          </h3>
        </div>
        <div className="modal-body">
          {errors && (
            <p className="text-danger mb-2">missing required fields!</p>
          )}
          <input
            className="exp-input"
            type="text"
            name="age"
            placeholder="age.."
            value={age}
            maxLength="3"
            onChange={onChangeHandler}
          />
          <input
            className="exp-input"
            name="graduated"
            placeholder="graduation year.."
            value={graduated}
            onChange={onChangeHandler}
            maxLength="4"
          />

          <div className="d-flex select-container">
            <input
              className="w-50 mr-1 exp-input"
              name="live_in_country"
              placeholder="country.."
              value={live_in_country}
              onChange={onChangeHandler}
              list="countries"
            />
            <datalist id="countries">
              <option value="Egypt" selected />
            </datalist>

            <input
              className="w-50 ml-1 exp-input"
              name="live_in_state"
              placeholder="state.."
              value={live_in_state}
              onChange={onChangeHandler}
              list="states"
            />
            <datalist id="states">
              {profile.states.map(st => (
                <option key={st} value={st}>
                  {st}
                </option>
              ))}
            </datalist>
          </div>
        </div>
        <div className="modal-footer">
          <button
            type="button"
            className="btn btn-secondary"
            data-dismiss="modal"
          >
            Close
          </button>
          <button onClick={onSave} type="button" className="btn btn-primary">
            Save changes
          </button>
        </div>
      </div>
    </div>
  </div>
);

export default class ExtraInfo extends Component {
  /* 
    basic_info: {
    age: Number,
    graduated: String,
    live_in: String
  },
  */
  constructor(props) {
    super(props);
    const { live_in, age, graduated } = props.basic_info || {
      live_in: "",
      age: "",
      graduated: ""
    };
    const lvArr = live_in.split(",");
    this.state = {
      age: age || "", //number not string
      graduated: graduated || "",
      live_in_country: lvArr[0] || "",
      live_in_state: lvArr[1] || ""
    };
  }

  onChangeHandler = ({ target }) => {
    if (
      (target.name === "age" || target.name === "graduated") &&
      (isNaN(target.value) ||
        (target.value.length === 1 && target.value === "0"))
    )
      return;
    this.setState({ ...this.state, [target.name]: target.value });
  };
  onSave = () => {
    const data = {
      age: parseInt(this.state.age),
      graduated: this.state.graduated,
      live_in: `${this.state.live_in_country}, ${this.state.live_in_state}`
    };
    //edit
    this.props._editProfile({ basic_info: data });
    $("#ExtraInfoModal").modal("hide");
  };
  componentDidUpdate(prevProps, prevState) {
    if (
      this.props.basic_info &&
      this.props.basic_info !== prevProps.basic_info
    ) {
      const { live_in, age, graduated } = this.props.basic_info;
      const lvArr = live_in.split(",");
      this.setState({
        ...this.state,
        age,
        graduated,
        live_in_country: lvArr[0],
        live_in_state: lvArr[1]
      });
    }
  }

  render() {
    const { basic_info, isPreviewing = false } = this.props;

    if (basic_info) {
      const {
        age = 0,
        graduated = "not selected",
        live_in = "not selected"
      } = basic_info;
      return (
        <div className="profile__extra-info main-card-layout relative-container">
          {!isPreviewing && (
            <button
              className="btn--add-plus btn btn-primary rounded-circle"
              type="button"
              data-toggle="modal"
              data-target="#ExtraInfoModal"
              style={{ zIndex: 10 }}
            >
              <i className="fas fa-pen text-light" />
            </button>
          )}

          <ExtraInfoModal
            onSave={this.onSave}
            {...this.state}
            onChangeHandler={this.onChangeHandler}
          />

          <div className="info">
            <img src="/images/age.svg" alt="" />
            <div className="content">
              <h6 className="info__title">Age</h6>
              <span className="text-muted">{age} years</span>
            </div>
          </div>
          <div className="info">
            <img className="  " src="/images/students-cap.svg" alt="" />
            <div className="content">
              <h6 className="info__title">Graduated from</h6>
              <span className="text-muted">{graduated}</span>
            </div>
          </div>
          <div className="info">
            <i className="fas fa-map" />
            <div className="content">
              <h6 className="info__title">live in</h6>
              <span className="text-muted">{live_in}</span>
            </div>
          </div>
        </div>
      );
    } else {
      return (
        <>
          <div className="profile__extra-info main-card-layout relative-container">
            {!isPreviewing ? (
              <>
                <button
                  className="btn--add-plus btn btn-primary rounded-circle"
                  type="button"
                  data-toggle="modal"
                  data-target="#ExtraInfoModal"
                  style={{ zIndex: 10 }}
                >
                  <i className="fas fa-plus text-light" />
                </button>
                <p className="mt-5">
                  please press plus button to add your basic info
                </p>
              </>
            ) : (
              "No Provided Data"
            )}
          </div>
          <ExtraInfoModal
            onSave={this.onSave}
            {...this.state}
            onChangeHandler={this.onChangeHandler}
          />
        </>
      );
    }
  }
}
