import React, { useState, useEffect } from "react";
import isEmpty from "../../../../utils/isEmpty";
import $ from "jquery";
import { withRouter } from "react-router-dom";

const PostJobModal = ({
  title,
  desc,
  requirements,
  experience,
  level,
  language,
  salary,
  type,
  open,
  _id,
  isEditProcess,
  jobLoaders,
  createJob,
  editJob,
  history
}) => {
  const [error, setError] = useState(false);
  const [isLoading, setLoading] = useState(null);
  const [job, setJob] = useState({
    title: "",
    desc: "",
    requirements: "",
    experience: "",
    level: "",
    language: "",
    salary: "",
    type: "",

    open: true
  });
  useEffect(() => {
    if (isEditProcess) {
      setJob({
        title,
        desc,
        requirements,
        experience,
        level,
        language,
        salary,
        type,
        open
      });
    }
  }, [
    title,
    desc,
    requirements,
    experience,
    level,
    language,
    salary,
    type,
    open
  ]);

  //loaders effect
  useEffect(() => {
    if (isEditProcess) {
      if (isLoading && jobLoaders.editingJob === false) {
        $(`#${_id}`).modal("hide");
      }
    } else {
      if (isLoading && jobLoaders.creatingJob === false) {
        $("#PostJobModal").modal("hide");
      }
    }
  }, [jobLoaders, jobLoaders.creatingJob, isLoading]);

  const onChangeHandler = e => {
    const target = e.target;
    const value = target.type === "checkbox" ? target.checked : target.value;
    const name = target.name;

    setJob(prevState => ({
      ...prevState,
      [name]: value
    }));
  };

  const onSave = () => {
    const {
      title,
      desc,
      requirements,
      experience,
      level,
      language,
      salary,
      type,
      open
    } = job;
    // should validate here
    if (isEmpty(title) || isEmpty(desc)) {
      setError(true);
      return;
    }
    setError(false);
    setLoading(true);

    const newJob = {
      title,
      desc,
      requirements,
      experience,
      level,
      language,
      salary,
      type,
      open
    };
    if (isEditProcess) {
      editJob(_id, newJob);
    } else {
      createJob(newJob);
    }
  };

  return (
    <div
      className="modal fade job-modal"
      id={isEditProcess ? _id : `PostJobModal`}
      tabIndex="-1"
      role="dialog"
      aria-labelledby="PostJobModalTitle"
      aria-hidden="true"
    >
      <div className="modal-dialog modal-dialog-centered" role="document">
        <div className="modal-content avoid-navbar">
          <div className="modal-header">
            <h5 className="modal-title" id="PostJobModalTitle">
              Create Job
            </h5>
          </div>
          <div className="modal-body">
            <div className="container-fluid">
              {error && (
                <p className="text-center text-danger">
                  missing required data denoted with ' * '
                </p>
              )}
              <form>
                <div className="row mb-3">
                  <div className="col font-weight-bold">Required Fields</div>
                </div>
                <div className="row mb-3">
                  <div className="col">
                    <input
                      onChange={onChangeHandler}
                      type="text"
                      className="form-control"
                      placeholder="Title*"
                      name="title"
                      value={job.title}
                    />
                  </div>
                </div>
                <div className="row mb-3">
                  <div className="col">
                    <textarea
                      style={{ resize: "none", height: 100 }}
                      onChange={onChangeHandler}
                      type="text"
                      className="form-control"
                      placeholder="Description*"
                      name="desc"
                      value={job.desc}
                    />
                  </div>
                </div>

                <hr />
                <div className="row mb-3">
                  <div className="col font-weight-bold">Optional Fields</div>
                </div>
                <div className="row mb-2">
                  <div className="col">
                    <textarea
                      style={{ resize: "none" }}
                      onChange={onChangeHandler}
                      type="text"
                      className="form-control"
                      placeholder="Requirements"
                      name="requirements"
                      value={job.requirements}
                    />
                  </div>
                </div>
                <div className="row mb-2">
                  <div className="col">
                    <input
                      onChange={onChangeHandler}
                      type="text"
                      className="form-control"
                      placeholder="Experience"
                      name="experience"
                      value={job.experience}
                    />
                  </div>
                </div>
                <div className="row mb-3">
                  <div className="col">
                    <input
                      onChange={onChangeHandler}
                      type="text"
                      className="form-control"
                      placeholder="Level"
                      name="level"
                      value={job.level}
                    />
                  </div>
                  <div className="col">
                    <input
                      onChange={onChangeHandler}
                      type="text"
                      className="form-control"
                      placeholder="Language"
                      name="language"
                      value={job.language}
                    />
                  </div>
                </div>

                <div className="row mb-3">
                  <div className="col">
                    <input
                      onChange={onChangeHandler}
                      type="text"
                      className="form-control"
                      placeholder="Salary"
                      name="salary"
                      value={job.salary}
                    />
                  </div>
                  <div className="col">
                    <input
                      onChange={onChangeHandler}
                      type="text"
                      className="form-control"
                      placeholder="Type"
                      name="type"
                      value={job.type}
                    />
                  </div>
                </div>
                <div className="row">
                  <div className="col">
                    <div className="form-group form-check d-flex align-items-center">
                      <input
                        onChange={onChangeHandler}
                        type="checkbox"
                        className="form-check-input "
                        id={isEditProcess ? _id + "isOpened" : "isOpened"}
                        name="open"
                        checked={!!job.open}
                      />
                      <label
                        className="form-check-label ml-3"
                        htmlFor={isEditProcess ? _id + "isOpened" : "isOpened"}
                      >
                        Job Is Opened
                      </label>
                    </div>
                  </div>
                  <div className="col-12">
                    <h6 className="">Note:</h6>
                    <p className="text-secondary">
                      you can create job quiz later after creating the job
                    </p>
                  </div>
                </div>
              </form>
            </div>
          </div>
          <div className="modal-footer">
            <button type="button" className="btn btn-info" data-dismiss="modal">
              Close
            </button>
            <button onClick={onSave} type="button" className="btn btn-primary">
              Save changes
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};
export default withRouter(PostJobModal);
