import React from "react";
import { Link } from "react-router-dom";
const PostCard = ({
  title = "post Title",
  desc = "post description",
  companyName = "company name",
  videoSrc = "",
  isBelonging,
  jobId,
  isPreview,
  src
}) => {
  return (
    <div className="postCard">
      <div className="d-flex flex-column flex-grow-1">
        <div className="postCard__header d-flex align-items-center">
          <div
            className="responsive_img_container "
            style={{ width: 50, height: 50 }}
          >
            <img className="" src={src} alt="profile image" />
          </div>
          <div className="postCard__header__text">
            <h5>{companyName}</h5>
          </div>
        </div>
        <h3 className="postCard__title">{title}</h3>
        <p>{desc}</p>
        {videoSrc && (
          <video style={{ height: 200 }} controls draggable src={videoSrc} />
        )}
      </div>
      {isBelonging && !isPreview ? (
        <div className="d-flex w-100">
          <Link
            to={"/create-job-quiz/" + jobId}
            className="btn btn-success btn-sm"
          >
            create quiz
          </Link>
          <Link to={"/job/" + jobId} className="btn btn-primary btn-sm">
            preview job
          </Link>
        </div>
      ) : (
        <Link className="btn btn-success">apply now</Link>
      )}
    </div>
  );
};

export default PostCard;
