import {
  FETCH_USER_FAILURE,
  FETCH_USER_START,
  FETCH_USER_SUCCESS,
  FETCH_PROFILE,
  FETCH_PROFILE_SUCCESS,
  FETCH_PROFILE_FAILURE
} from "../../types";
import Axios from "axios";
import { uris } from "../../../config";
import { fetchProfile } from "../";

export const fetchUserData = () => dispatch => {
  dispatch({
    type: FETCH_USER_START
  });
  dispatch({
    type: FETCH_PROFILE
  });
  Axios.get(uris.user.getUserData, {
    timeout: 9999999,
    headers: {
      Authorization: localStorage.token
    }
  })
    .then(({ data }) => {
      dispatch({
        type: FETCH_USER_SUCCESS,
        payload: data.user
      });

      dispatch({
        type: FETCH_PROFILE_SUCCESS,
        payload: { data: { ...data }, isViewingProfile: false }
      });
    })
    .catch(e => {
      dispatch({
        type: FETCH_USER_FAILURE
      });
      dispatch({ type: FETCH_PROFILE_FAILURE });
      console.error(e);
    });
};
