import React, { Component } from "react";
import uuid from "uuid";
import { educationValidator } from "../../../../utils/validation/validateProfile";
import DatePicker from "../../../reusable/DatePicker";
import $ from "jquery";
import BarLoader from "../../../reusable/loaders/BarLoader";
import moment from "moment";
import { profile } from "../../../../utils/fixtures";

const EduModal = ({
  title,
  at,
  schoolCountry,
  schoolState,
  onChangeHandler,
  getDateStateFromDatePicker,
  onSave,
  errors
}) => (
  <div
    className="modal fade avoid-navbar exp-modal"
    id="EduModal"
    tabIndex="-1"
    role="dialog"
    aria-labelledby="experienceModalTitle"
    aria-hidden="true"
  >
    <div className="modal-dialog modal-dialog-scrollable" role="document">
      <div className="modal-content">
        <div className="modal-header">
          <h2 className="modal-title" id="experienceModalTitle">
            Add Education
          </h2>
        </div>
        <div className="modal-body">
          {errors && (
            <p className="text-danger mb-2">missing required fields!</p>
          )}
          <input
            className="exp-input"
            name="title"
            placeholder="study title.."
            value={title}
            onChange={onChangeHandler}
          />
          <input
            className="exp-input"
            name="at"
            placeholder="study place name.."
            value={at}
            onChange={onChangeHandler}
          />

          <div className="d-flex select-container">
            <input
              className="w-50 mr-1 exp-input"
              name="schoolCountry"
              placeholder="country.."
              value={schoolCountry}
              onChange={onChangeHandler}
              list="countries"
            />
            <datalist id="countries">
              <option value="Egypt" selected />
            </datalist>

            <input
              className="w-50 ml-1 exp-input"
              name="schoolState"
              placeholder="state.."
              value={schoolState}
              onChange={onChangeHandler}
              list="governs"
            />
            <datalist id="governs">
              {profile.states.map(st => (
                <option key={st} value={st}>
                  {st}
                </option>
              ))}
            </datalist>
          </div>

          <DatePicker updateCallerState={getDateStateFromDatePicker} />
        </div>
        <div className="modal-footer">
          <button
            type="button"
            className="btn btn-secondary"
            data-dismiss="modal"
          >
            Close
          </button>
          <button onClick={onSave} type="button" className="btn btn-primary">
            Save changes
          </button>
        </div>
      </div>
    </div>
  </div>
);

const Edu = ({
  title,
  at,
  location,
  from,
  to,
  index,
  _deleteProfileField,
  isPreviewing
}) => (
  <div className="education">
    <div className="d-flex w-100 justify-content-between deletion-container">
      <h6 className="education__title">{title}</h6>
      {!isPreviewing && (
        <i
          className="fas fa-times btn--danger-delete mr-5"
          onClick={() => {
            _deleteProfileField("education", index);
          }}
        />
      )}
    </div>
    <h6 className="education__subtitle">{at}</h6>
    <small className="text-muted">{location}</small>
    <small className="education__date text-muted">
      {moment(from).format("MMM YYYY")} - {moment(to).format("MMM YYYY")}
    </small>
  </div>
);

export class Education extends Component {
  /* 
      at:String,
      titld:String,
      location:String,
      from:Date,
      to:Date 
  */

  constructor(props) {
    super(props);
    const {
      title,
      schoolCountry,
      at,
      schoolState,
      startDate = null,
      endDate = null
    } = props;
    this.state = {
      title: title || "",
      at: at || "",
      schoolCountry: schoolCountry || "",
      schoolState: schoolState || "",
      startDate: startDate || null,
      endDate: endDate || null,
      errors: false
    };
  }
  clearState = () =>
    this.setState({
      title: "",
      schoolCountry: "",
      at: "",
      schoolState: "",
      startDate: null,
      endDate: null,
      errors: false
    });

  componentDidMount() {
    const that = this;
    $("#EduModal").on("hidden.bs.modal", () => {
      that.clearState();
    });
  }

  onChangeHandler = ({ target }) => {
    this.setState({ ...this.state, [target.name]: target.value });
  };

  getDateStateFromDatePicker = (startDate = undefined, endDate = undefined) =>
    this.setState({ ...this.state, startDate, endDate });

  onSave = () => {
    const { isValid } = educationValidator(this.state);
    if (!isValid) {
      this.setState({ ...this.state, errors: true });
      return;
    }

    const data = {
      title: this.state.title,
      at: this.state.at,
      location: `${this.state.schoolCountry} , ${this.state.schoolState}`,
      from: this.state.startDate,
      to: this.state.endDate
    };
    //edit
    this.props._editProfile({ education: data });
    $("#EduModal").modal("hide");
  };

  render() {
    const { educations = [], isPreviewing = false } = this.props;
    const { errors } = this.state;
    return (
      <div className="profile__education main-card-layout relative-container">
        <h6 className="main-card-layout__title">Education</h6>
        {!isPreviewing && (
          <>
            <button
              className="btn--add-plus btn btn-primary rounded-circle"
              type="button"
              data-toggle="modal"
              data-target="#EduModal"
            >
              <i className="fas fa-plus text-light" />
            </button>
            <EduModal
              {...this.state}
              onChangeHandler={this.onChangeHandler}
              getDateStateFromDatePicker={this.getDateStateFromDatePicker}
              onSave={this.onSave}
              errors={errors}
            />
          </>
        )}
        {educations === null ? (
          <BarLoader />
        ) : educations.length > 0 ? (
          educations.map((edu, index) => (
            <Edu
              _deleteProfileField={this.props._deleteProfileField}
              key={uuid()}
              {...edu}
              index={index}
              isPreviewing={isPreviewing}
            />
          ))
        ) : (
          "No education added"
        )}
      </div>
    );
  }
}
export default Education;
